<template>
    <view>
        <view class="grid-box">
            <view class="grid-box-item" :style="{ width: gridWidth + '%' }" v-for="(value,i) in rdata.params.list" :key="i" @click="jump(value)">
                <text class="grid-box-item-text">{{ value.text }}</text>
            </view>
        </view>
    </view>
</template>

<script>
    export default {
        data(){
            return {
                gridWidth:{}
            };
        },
        props: {
            rdata:{
                required: true,
            }
        },
        created() {
            let value = "33.333";
            if(this.rdata.params.limit == 4){
                value = "25";
            }else if(this.rdata.params.limit == 5){
                value = "20";
            }

            this.gridWidth = value;
        },
        methods: {
            jump(value){
                this.$common.jump(value);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .grid-box{
		background: #fff;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		padding-top: 20rpx;
		.grid-box-item {
			width: 25%;
			height: auto !important;
			height: 60rpx;
			min-height: 60rpx;
			text-align: center;
			.grid-box-item-text {
				display: block;
				font-size: 26rpx;
				color: #666;
				width: 100%;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
	}
</style>
